.fade-in-section {
    opacity: 0;
    transform: translateY(20px);
    animation: fadeIn 1s forwards;
    animation-delay: 0.5s;
}

.fade-in-section + .fade-in-section {
    animation-delay: 1.5s; 
}

@keyframes fadeIn {
    to {
        opacity: 1;
        transform: none;
    }
}
